body {
  width: 35%;
}

.habit-nav {
  background-color: aliceblue;
  font-size: 1.5rem;
  padding: 0.5em;
}

.habit-nav-title {
  display: inline-block;
  padding-left: 0.5em;
}

.fa-leaf {
  color: green;
}

.habit-countAll {
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  margin-left: 0.2rem;
  margin-right: 0.5rem;
  background-color: green;
  color: white;
  border-radius: 50%;
}

.habit-input {
  margin-top: 25px;
  margin-left: 20px;
}

.habit-input-text {
  width: 380px;
  height: 30px;
  font-size: 16px;
}

.habit-addBtn {
  background-color: green;
  color: white;
  height: 36px;
  margin-left: 10px;
  font-size: 20px;
}

button {
  outline: none;
  border: 0;
  cursor: pointer;
}

.habit-button {
  font-size: 2.5rem;
  margin: 0 0.1em;
  background-color: transparent;
}

.habit {
  font-size: 2.5rem;
  padding: 0.5em;
  list-style: none;
}

.habit-count {
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  margin-left: 0.2rem;
  margin-right: 0.5rem;
  background-color: darkseagreen;
  color: white;
  border-radius: 50%;
}

.habit-button:hover {
  opacity: 0.8;
}

.habit-increase,
.habit-decrease {
  color: goldenrod;
}

.habit-delete {
  font-size: 2rem;
  color: darkred;
}
